.icon-right {
  &__photo {
    transition: fill .15s linear;
    fill: rgba(62, 70, 94, .8);
    width: 2rem;
    height: 2rem;
    cursor: pointer;
    border: none;
    &_hidden {
      display: none;
    }
  }
  &__photo:hover {
    fill: #000;
  }
  &__mic {
    transition: fill .15s linear;
    fill: rgba(62, 70, 94, .8);
    width: 2rem;
    height: 2rem;
    cursor: pointer;
    border: none;
    &_hidden {
      display: none;
    }
  }
  &__mic:hover {
    fill: #000;
  }
}

@media screen and (max-width: 400px) {
  .icon-right {
    z-index: 3;
    background-color: #efefee;
  }
}