.modal {
    width: 100%;
    height: 100%;
    z-index: 1000;
    position: absolute;
    left: 0;
    bottom: 0;

    &__background {
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        bottom: 0;
        background-color: rgba(0, 0, 0, 0.2);
    }

    &__popup {
        position: fixed;
        left: 0;
        bottom: 0;
        width: 100%;
        background-color: #ffffff;
        box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    }

    &__full {
        height: 96%;
    }

    &__half {
        height: 40%;
    }


    &__head {
        display: flex;
        flex-direction: row;
        justify-content: right;
        align-items: flex-start;
    }

    &__close {
        cursor: pointer;
        position: absolute;
        background: url('../../assets/icons/delete.svg');
        background-position: center;
        background-repeat: no-repeat;
        background-size: 3rem;
        width: 3rem;
        height: 3rem;
        border: 0rem;
        opacity: 0.5;
        z-index: 1000;
        border-radius: 1rem;
    }

    &__close:hover {
        opacity: 1;
    }
}

@media screen and (max-width: 768px) {
    .modal{
        &__close {
            right: 0.8rem;
            top: 1rem;
            background: url('../../assets/icons/delete.svg');
            background-position: center;
            background-repeat: no-repeat;
            background-size: 3rem;
            background-color: #333333;
            width: 3rem;
            height: 3rem;
            border-radius: 2rem;
            border: 0.1rem solid #333333;
        }
    
        &__close:hover {
            opacity: 0.3;
        }

        &____popup {
            background-color: black;
        }

        &__full {
            height: 100%;
        }
    }

}