.hintList {
    width: 100%;
    padding: 0.14rem;
    display: flex;
    flex-direction: column;
    border-radius: 1rem 1rem 0 0;
    background-color: white;
    list-style: none;
    &_hidden {
        display: none;
    }
    &_only {
        border-radius: 1rem;
    }
}

@media screen and (max-width: 768px) {
    .hintList {
        position: static;
        flex-wrap: wrap;
        flex-direction: row;
        gap: 0.75rem;
        padding: 0.5rem 1rem;
    }
}