.searchbar {
    position: relative;
    font-size: 1rem;
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    width: 100%;
    height: 3rem;
    overflow: hidden;
    &__back {
      display: grid;
      place-content: center;
      width: 3rem;
      height: 3rem;
      border: none;
      border-radius: 1rem;
      background: rgba(92, 90, 87, 0.1);
      cursor: pointer;
      &::before {
        width: 1rem;
        height: 1rem;
        content: url('../../assets/icons/back.svg');
      }
    }
    &__hint-sample {
      position: absolute;
      visibility: hidden;
      pointer-events: none;
    }
    &__hint {
      position: absolute;
      max-width: 70%;
      white-space: nowrap;
      overflow: hidden;
      color: lightgray;
    }
    &__icon-right {
        position: absolute;
        right: 0.375rem;
        display: flex;
        gap: .2rem;
      }
    &__input {
        position: relative;
        flex-grow: 1;
        display: flex;
        align-items: center;
        height: 100%;
        padding: 0 0.875rem;
        border: 2px solid #FCE000;
        border-radius: 1rem 0 0 1rem;
      .Textinput-Control {
        font-family: inherit;
        font-size: 1rem;
        max-width: 80%;
      }
      .Textinput-Clear {
        top: 4px;
        right: 0.25rem !important;
        background-image: url('../../assets/icons/delete.svg');
        background-size: 2.6rem;
        background-repeat: no-repeat;
        &:hover {
            opacity: .45;
        }
      }
      &::before {
        width: 1.5rem;
        height: 1.5rem;
        content: url('../../assets/icons/search.svg');
        transform: translateY(1px);
      }
      .Textinput-Box {
        display: none;
      }
    }
    &__search-button {
      font-family: inherit;
      width: 7rem;
      height: 100%;
      background-color: #FCE000;
      border-radius: 0 1rem 1rem 0;
      &:hover {
        background-color: #F2D700;
        & + .searchbar__input {
          border-color: #F2D700;
        }
      }
    }
    &_focused {
      .searchbar__search-button {
        border-radius: 1rem;
        border: 2px solid white;
      }
      .searchbar__input {
        border: none;
      }
    }
  }

  @media screen and (max-width: 768px) {
    .searchbar {
      border-radius: 1rem;
      background-color: rgba(92, 90, 87, 0.1);
      &__input {
        border: none;
        &::before {
          width: 1.3125rem;
          height: 1.3125rem;
        }
      }
      .Textinput-Clear {
        right: 0.875rem !important;
        top: 6px;
      }
      &__icon-right {
        right: 0.875rem;
      }
    }
  }

  @media screen and (max-width: 660px) {
    .searchbar {
      .Textinput-Control {
        max-width: 80%;
      }
    }
  }

  @media screen and (max-width: 500px) {
    .searchbar {
      .Textinput-Control {
        max-width: 76%;
      }
    }
  }

  @media screen and (max-width: 400px) {
    .searchbar {
      .Textinput-Control {
        max-width: 73%;
      }
    }
  }

  @media screen and (max-width: 350px) {
    .searchbar {
      .Textinput-Control {
        max-width: 65%;
      }
    }
  }
