.restaurant {
    color: black;
    text-decoration: none;
    transition: filter .30s linear;
    &:hover {
        .restaurant__photo {
            filter: brightness(1.1);
        }
    }
    &__photo {
        width: 100%;
        height: 0;
        padding-bottom: 59%;
        margin-bottom: 0.45rem;
        border-radius: 1.5rem;
        background-position: center;
        background-size: cover;
        &_1 {
            background-image: url('../../assets/images/rest_1.webp');
        }
        &_2 {
            background-image: url('../../assets/images/rest_2.webp');
        }
        &_3 {
            background-image: url('../../assets/images/rest_3.webp');
        }
        &_4 {
            background-image: url('../../assets/images/rest_4.webp');
        }
        &_5 {
            background-image: url('../../assets/images/rest_5.webp');
        }
        &_6 {
            background-image: url('../../assets/images/rest_6.webp');
        }
        &_7 {
            background-image: url('../../assets/images/rest_7.webp');
        }
    }
    &__badge {
        display: flex;
        align-items: center;
        position: relative;
        top: 0.5rem;
        left: 0.5rem;
        width: fit-content;
        padding: 0.3125rem;
        padding-right: 0.5rem;
        border-radius: 1.3rem;
        background-color: rgba(255, 255, 255, 0.8);
        &-icon {
            width: 1.5rem;
            height: 1.5rem;
            border-radius: 50%;
            background-color: rgb(90, 195, 26);
            background-size: cover;
            background-position: center;
            margin-right: 0.3125rem;
            &_gift {
                background-image: url('../../assets/icons/gift.png');
            }
            &_discount {
                background-image: url('../../assets/icons/discount.png');
            }
            &_delivery {
                background-image: url('../../assets/icons/delivery.png');
            }
        }
        &-text {
            font-size: 0.875rem;
            font-weight: 700;
        }
    }
    &__name {
        margin-bottom: 0.25rem;
    }
    &__info {
        display: flex;
    }
    &__rating-icon {
        width: 0.875rem;
        height: 0.875rem;
        margin-right: 0.3125rem;
        background-position: center;
        background-size: cover;
        &_good {
            background-image: url('../../assets/icons/yellow_star.svg');
        }
        &_recommend {
            background-image: url('../../assets/icons/fire.svg');
        }
        &_excellent {
            background-image: url('../../assets/icons/green_star.svg');
        }
    }
}