.header {
    position: relative;
    z-index: 5;
    display: flex;
    align-items: center;
    gap: 2.25rem;
    padding: 1rem 0;
    box-shadow: 0 1px rgba(255, 255 , 255, 0.12);
    border-bottom: 1px solid #D2D0CC;
    &__logo {
        width: 9.25rem;
        height: 1.625rem;
        background: url('./../../assets/images/logo.svg') no-repeat;
        background-size: 9.25rem auto;
    }
}

@media screen and (max-width: 1024px) {
    .header {
        &__logo {
            width: 1.875rem;
            height: 1.875rem;
            background-position: -90px 50%;
            background-size: auto 1.875rem;
        }
    }
}

@media screen and (max-width: 768px) {
    .header { 
        box-shadow: none;
        border-bottom: 0;
        &__logo {
            display: none;
        }
    }
}

@media screen and (max-width: 536px) {
    .header { 
        box-shadow: none;
        border-bottom: 0;
        &__logo {
            display: none;
        }
    }
}

@media screen and (max-width: 426px) {
    .header { 
        box-shadow: none;
        border-bottom: 0;
        &__logo {
            display: none;
        }
    }
}