.suggestion {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 0.75rem;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
  border-radius: 1rem;
  cursor: pointer;
  &__photo {
      width: 100%;
      height: 0;
      padding-bottom: 100%;
      margin-bottom: 0.75rem;
      border-radius: 1rem;
      background-size: cover;
      background-position: center;
      &_RESTAURANT_ITEM {
          background-image: url('../../assets/images/restaurant_item.jpg');
      }

      &_GROCERY_ITEM {
          background-image: url('../../assets/images/grocery_item.jpg');
      }
      &_PLACE {
          background-image: url('../../assets/images/place.jpg');
      }
  }
  &__info {
      display: flex;
      flex-direction: column;
      flex-grow: 1;
  }
  &__name {
      font-size: 1rem;
      line-height: 1.5rem;
      max-height: 4.5rem;
      margin-bottom: 0.8rem;
      word-break: break-word;
      overflow: hidden;
      text-overflow: ellipsis;
      &::first-letter {
          text-transform: uppercase;
      }
  }
  &__place {
      color: rgba(62, 70, 94, 0.8);
      font-size: 1rem;
      font-weight: 400;
      word-break: break-word;
  }

  &__button {
      color: rgb(33, 32, 31);
      background-color: rgba(92, 90, 87, 0.1);
      width: 100%;
      margin: 0.75rem 0 0;
      padding: 0.75rem 0.5rem;
      border-radius: 0.75rem;
      border: none;
      align-self: center;
      text-align: center;
      cursor: pointer;

      &:hover {
          background-color: #E7E6E4
      }
  }
}

@media screen and (max-width: 768px) {
  .suggestion {
      flex-direction: row;
      align-items: center;
      box-shadow: 0px 4px 20px rgb(0 0 0 / 12%);

      &__photo {
          width: 10%;
          min-width: 50px;
          height: 0;
          padding-bottom: max(50px, 10%);
          background-size: cover;
          background-position: center;
          margin: 0 1rem 0 0;
      }

      &__name {
          margin-bottom: 0.4rem;
      }

      &__place {
          margin: 0;
      }

      &__button {
          display: none;
      }
  }
}