.audio {
  position: absolute;
  bottom: 18;
  width: 100%;
  height: 100%;
  min-width: 300px;
  min-height: 120px;
  background-color: white;
  color: #000;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  font-size: 2rem;

  &__title {
    text-align: center;
  }

  &__char {
    display: inline-block;
    animation-name: start-text-animation;
    animation-duration: .3s;
    animation-iteration-count: infinite;
    animation-direction: alternate;
  }
}

@keyframes start-text-animation {
  to {
    transform: translateY(-8pt)
  }
}
