.app {
    padding: 0 1.5rem;
    height: 100vh;
}

@media screen and (max-width: 1024px) {
    .app {
        padding: 0 1.25rem;
    }
}

@media screen and (max-width: 768px) {
    .app {
        padding: 0 1rem;
    }
}

@media screen and (max-width: 536px) {
    .app {
        padding: 0 0.75rem;
    }
}

@media screen and (max-width: 425px) {
    .app {
        padding: 0 0.5rem;
    }
}

