.search {
    position: relative;
    width: 50%;
    display: flex;
    flex-direction: column;
    border-radius: 1rem;
    transition: box-shadow 0.2s ease;
    &_focused {
        box-shadow: 0 0 0 9999px #393838b0;
    }
    &__content {
        width: 100%;
        position: absolute;
        top: 3.5rem;
        left: 0;
        z-index: 1;
        display: flex;
        flex-direction: column;
    }
}

@media screen and (max-width: 1024px) {
    .search {
        width: 75%;
        &__content {
            border-radius: 2rem;
            box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;
        }
    }
}

@media screen and (max-width: 768px) {
    .search {
        width: 100%;
        &_focused {
            box-shadow: none;
        }
        &__content {
            border-radius: 2rem;
            box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;
        }
    }
}