.hint {
    width: 100%;
    height: 3rem;
    padding: 0.5rem 1rem;
    display: flex;
    align-items: center;
    border-radius: 1rem;
    white-space: nowrap;
    overflow: hidden;
    cursor: pointer;
    &_selected {
        background-color: rgba(45,45,45, 0.12);
    }
    &__history {
        background: url('../../assets/icons/history.svg');
        background-position: center;
        background-size: cover;
        width: 1.2rem;
        height: 1.2rem;
        margin-right: 0.5rem;
    }
    &__text {
        overflow: hidden;
        font-size: 1rem;
        font-family: inherit;
        font-weight: 400;
        span {
            font-weight: bold;
        }
        &_category {
            &::before {
            content: 'Категория: ';
            color: gray;
            }
        }
    }
    &__icon {
        width: 1rem;
        height: 1rem;
        margin-left: auto;
        margin-right: 0;
        border: none;
        cursor: pointer;
        &_delete {
            background: url('../../assets/icons/delete.svg');
            background-position: center;
            background-size: 175% 175%;
        }
        &_link {
            background: url('../../assets/icons/link.svg');
            background-position: center;
            background-size: cover;
        }
        &:hover {
            filter: brightness(0.7);
        }
    }
}

@media screen and (max-width: 768px) {
    .hint {
        width: fit-content;
        height: 2.75rem;
        background-color: rgb(230,230,230);
        border-radius: 1.5rem;
        transition: transform 0.1s ease;
        &:active {
            transform: scale(0.98)
        }
        &__icon {
            display: none;
        }
    }
}