.suggestionsList {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-column-gap: 0.5rem;
    padding: 0.25rem 0.5rem 0.5rem;
    border-radius: 0 0 1rem 1rem;
    background-color: white;
    list-style: none;
    &_hidden {
        display: none;
    }
    &_only {
        border-radius: 1rem;
        padding: 0.5rem;
    }
}


@media screen and (max-width: 768px) {
    .suggestionsList { 
        display: flex;
        flex-direction: column;
        gap: 0.5rem;
        &_hidden {
            display: none;
        }
    }
    .suggestionsList_hidden {
        display: none;
    } 
}
