.photo {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  width: 50vw;
  min-width: 320px;
  transform: translate(50%, 20px);

  &__card {
    position: absolute;
    bottom: -12rem;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    border-radius: 1rem;
    gap: 1rem;
    padding: 0.3rem;
  }

  &__word {
    font-size: 2rem;
    height: 2rem;
    color: #21201F;
  }

  &__plus {
    font-size: 2rem;
    height: 2rem;
    color: #21201F;
    background-color: rgb(245, 244, 242);
    border-radius: 1rem;
    width: 3rem;
    height: 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__plus:hover {
    opacity: 0.5;
    cursor: pointer;
  }

  &__canvas {
    width: 10rem;
    height: 10rem;
    border-radius: 1rem;
    display: none;
  }

  &__button {
    position: absolute;
    margin: 0;
    width: 4rem;
    height: 4rem;
    background-color: #F5F4F2;
    border: 0.3rem solid rgb(146, 38, 254);
    border-radius: 1.6rem;
    bottom: -18rem;
    background-image: url('../../assets/icons/photo.svg');
    background-repeat: no-repeat;
    background-position: center;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
    cursor: pointer;
  }

  &__button:hover {
    opacity: 0.8;
  }

  &__button:active {
    opacity: 0.5;
  }
}

.photo__body {
  display: flex;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  border-radius: 1rem;
  overflow: hidden;
  width: 100%;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
  background-color: #21201F;
}

.photo__frame {
  position: absolute;
  top: 0;
  z-index: 1000;
  height: 100%;
  width: 100%;
  border-radius: 1rem;
  padding: 4rem;
}

.photo__target {
  z-index: 1000;
  width: 100%;
  height: 100%;
  border-radius: 0.4rem;
  background:
    linear-gradient(to right, black 0.3rem, transparent 0.3rem) 0 0,
    linear-gradient(to right, black 0.3rem, transparent 0.3rem) 0 100%,
    linear-gradient(to left, black 0.3rem, transparent 0.3rem) 100% 0,
    linear-gradient(to left, black 0.3rem, transparent 0.3rem) 100% 100%,
    linear-gradient(to bottom, black 0.3rem, transparent 0.3rem) 0 0,
    linear-gradient(to bottom, black 0.3rem, transparent 0.3rem) 100% 0,
    linear-gradient(to top, black 0.3rem, transparent 0.3rem) 0 100%,
    linear-gradient(to top, black 0.3rem, transparent 0.3rem) 100% 100%;

  background-repeat: no-repeat;
  background-size: 2rem 2rem;
}

@media screen and (max-width: 768px) {
  .photo {
    background-color: black;
    width: 100vw;
    height: 100vh;
    transform: translate(0, 0);
  }

  .photo__frame {
    height: 40vh;
  }

  .photo__target {
    height: 40vh;
  }

  .photo__body {
    border-radius: 0rem;
  }

  .photo__card {
    bottom: 10rem;
  }

  .photo__word {
    color: #ffffff;
  }

  .photo__plus {
    font-size: 2rem;
    height: 4rem;
    width: 4rem;
    color: #21201F;
  }

  .photo__button {
    bottom: 2rem;
  }
}