.main {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width: 100%;
    padding: 2rem 0;
    gap: 4rem;
    &_focused {
        pointer-events: none;
    }
}

.section {
    &__header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 2rem;
    }
    &__title {
        font-size: 3rem;
        font-weight: bolder;
        line-height: 3rem;
        overflow: hidden;
        margin: 0;
        white-space: nowrap;
        text-overflow: ellipsis;
    }
    &__show-all {
        line-height: 1.5rem;
        padding: 0.72rem 1.125rem;
        cursor: pointer;
        color: black;
        border: none;
        border-radius: 0.75rem;
        background-color: #f1f0ed;
        text-decoration: none;
        &::after {
            margin-left: 0.625rem;
            content: url('../../assets/icons/arrow-right.svg');
        }
    }
    &__subtitle {
        font-size: 24px;
        font-weight: 900;
        line-height: 48px;
        overflow: hidden;
        padding-top: 1rem;
        white-space: nowrap;
        text-overflow: ellipsis;
    }
    &__list {
        display: grid;
        width: 100%;
        grid-template-columns: repeat(5, 1fr);
        gap: 1rem;
        list-style: none;
        &_expanded {
            grid-template-columns: repeat(4, 1fr);
            gap: 1.5rem;
        }
    }
    &__item {
        height: 0;
        padding-bottom: 60%;
        cursor: pointer;
        transition: filter .2s ease-out;
        border-radius: 1rem;
        background-position: center;
        background-size: cover;
        &:hover {
            filter: brightness(1.1);
        }
        &_1 {
            background-image: url('../../assets/images/store_1.webp');
        }
        &_2 {
            background-image: url('../../assets/images/store_2.webp');
        }
        &_3 {
            background-image: url('../../assets/images/store_3.webp');
        }
        &_4 {
            background-image: url('../../assets/images/store_4.webp');
        }
        &_5 {
            background-image: url('../../assets/images/store_5.webp');
        }
    }
}

@media screen and (max-width: 1024px) {
    .section {
        &__list {
            grid-template-columns: repeat(4, 1fr);
            gap: 0.75rem;
            &_expanded {
                grid-template-columns: repeat(3, 1fr);
                gap: 1.25rem;
            }
        }
    }
}

@media screen and (max-width: 768px) {
    .section {
        &__list {
            grid-template-columns: repeat(3, 1fr);
            gap: 0.5rem;
            &_expanded {
                grid-template-columns: repeat(2, 1fr);
                gap: 1rem;
            }
        }
    }
}

@media screen and (max-width: 536px) {
    .section {
        &__list {
            grid-template-columns: repeat(2, 1fr);
            gap: 0.5rem;
            &_expanded {
                display: flex;
                flex-direction: column;
                gap: 2rem;
            }
        }
    }
}


@media screen and (max-width: 375px) {
    .section {
        &__list {
            display: flex;
            flex-direction: column;
            gap: 1rem;
            &_expanded {
                gap: 2rem;
            }
        }
    }
}

@media screen and (max-width: 375px) {
    .section {
        &__title {
            font-size: 2.5rem;
        }
    }
}